.contact-user {
  width: 100%;
  min-height: 162px;
  border: 2px solid #e5e5e5;
  border-radius: 3px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-bottom: 61px;
}
.contact-user--mobile {
  border: none;
  margin: 0;
  padding: 0;
}
.contact-user__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-left: 58px;
  padding-right: 58px;
  padding-top: 38px;
  padding-bottom: 23px;
}
.contact-user__wrapper--tablet {
  padding-left: 60px;
  padding-right: 60px;
  padding-top: 45px;
  padding-bottom: 33px;
}
.contact-user__wrapper--mobile {
  padding-top: 45px;
  padding-bottom: 26px;
}
.contact-user__wrapper p {
  position: relative;
  width: 375px;
  margin: 0;
}
.contact-user__wrapper p--tablet {
  width: 497px;
}
.contact-user__wrapper p--mobile {
  width: 280px;
  margin: 0 auto;
}
.contact-user__wrapper label {
  display: block;
  margin-top: 19px;
  margin-left: 20px;
  text-align: left;
}
.contact-user__wrapper label--mobile {
  margin-left: 20px;
  margin-bottom: 15px;
}
.contact-user__wrapper span {
  position: absolute;
  display: flex;
  width: 60px;
  height: 58px;
  top: 0;
  left: 312px;
  background-color: #e5e5e5;
  border-top: 2px solid #e5e5e5;
  border-right: 2px solid #e5e5e5;
  border-bottom: 2px solid #e5e5e5;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  z-index: -1;
}
.contact-user__wrapper span--tablet {
  left: 436px;
}
.contact-user__tel {
  box-sizing: border-box;
  width: 375px;
  border: 2px #e5e5e5 solid;
  border-radius: 3px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
  font-family: inherit;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
  background-color: transparent;
  white-space: normal;
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: 332px 50%;
}
.contact-user__tel--tablet {
  width: 497px;
  background-position: 456px 50%;
}
.contact-user__tel--mobile {
  width: 236px;
}
.contact-user__mail {
  box-sizing: border-box;
  width: 375px;
  border: 2px #e5e5e5 solid;
  border-radius: 3px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 14px;
  padding-bottom: 14px;
  font-family: inherit;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
  white-space: normal;
  background-image: url("../img/icon-mail.svg");
  background-repeat: no-repeat;
  background-position: 332px 50%;
}
.contact-user__mail--tablet {
  width: 497px;
  background-position: 456px 50%;
}
.contact-user__mail--mobile {
  width: 236px;
}
.progress-trip {
  width: 100%;
  min-height: 162px;
  border: 2px solid #e5e5e5;
  border-radius: 3px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin-bottom: 61px;
}
.progress-trip--mobile {
  padding: 0;
}
.progress-trip__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  margin-top: 52px;
  margin-left: 60px;
}
.progress-trip__list--mobile {
  display: flex;
  flex-wrap: wrap;
  width: 280px;
  margin: 0;
  padding: 0;
  margin-top: 41px;
  transform: translateX(90px);
}
.progress-trip__item {
  width: 230px;
  align-items: center;
  font-family: inherit;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
  list-style: none;
  margin-bottom: 65px;
  text-align: left;
  margin-left: 60px;
}
.progress-trip__item--tablet {
  width: 230px;
  align-items: center;
  font-family: inherit;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
  list-style: none;
  margin-bottom: 65px;
  text-align: left;
  margin-left: 60px;
}
.progress-trip__item--mobile {
  width: 280px;
  font-family: inherit;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: #283645;
  list-style: none;
  margin-bottom: 65px;
  text-align: left;
}
.progress-trip__list label {
  position: relative;
}
.progress-trip__list input[type="checkbox"]:checked + label::after {
  position: absolute;
  display: inline-block;
  vertical-align: middle;
  content: "";
  top: 5px;
  left: -47px;
  width: 21px;
  height: 10px;
  border-left: solid 2px #d22856;
  border-bottom: solid 2px #d22856;
  transform: rotate(-45deg);
  opacity: 1;
}
.progress-trip__list input[type="checkbox"] + label::after {
  position: absolute;
  display: inline-block;
  vertical-align: middle;
  content: "";
  top: 5px;
  left: -47px;
  width: 21px;
  height: 10px;
  border-left: solid 2px #d22856;
  border-bottom: solid 2px #d22856;
  transform: rotate(-45deg);
  opacity: 0;
}
.emotions {
